<template>
  <div v-if="hotel" class="details-container">
    <!-- Ìò∏ÌÖî Ïù¥ÎØ∏ÏßÄ -->
    <div class="img-container">
      <template v-if="hotel.imageUrl">
        <img :src="hotel.imageUrl" alt="Hotel Image" />
      </template>
      <template v-else>
        <div class="no-image-container">
          <p class="no-image-text">ÏóÖÏ≤¥Ï∏°ÏóêÏÑú Ï†úÍ≥µÎêú Ïù¥ÎØ∏ÏßÄÍ∞Ä ÏóÜÏäµÎãàÎã§</p>
        </div>
      </template>
    </div>

    <!-- Ìò∏ÌÖî Ï†ïÎ≥¥ -->
    <div class="hotel-info-card">
      <div class="hotel-top">
        <div>
          <div class="hotel-name">{{ hotel.name }}</div>
        </div>
        <div class="favorite-container" v-if="isLoggedIn">
          <div 
            class="heart-button" 
            :class="{'favorited': isFavorited, 'unfavorited': !isFavorited}" 
            @click="toggleFavorite">
            <i class="fas fa-heart" v-if="isFavorited"></i>
            <i class="far fa-heart" v-else></i>
          </div>
        </div>
      </div>
      <div class="hotel-info">
        <span class="rating">‚≠ê {{ hotel.rating || "0" }}</span>
        <span>({{ hotel.reviewCount || 0 }} Î¶¨Î∑∞)</span>
      </div>
      <div class="hotel-info-details">
        <p>
          <span class="phone-icon">üìû</span>
          Ï†ÑÌôîÎ≤àÌò∏ : {{ hotel.hotelnum || "ÏóÖÏ≤¥Ï∏°ÏóêÏÑú Ï†úÍ≥µÎêú Ï†ïÎ≥¥Í∞Ä ÏóÜÏäµÎãàÎã§." }}
        </p>
        <p>
          <span class="location-icon">üìç</span> 
          {{ hotel.address || "ÏóÖÏ≤¥Ï∏°ÏóêÏÑú Ï†úÍ≥µÎêú Ï†ïÎ≥¥Í∞Ä ÏóÜÏäµÎãàÎã§." }}
          <button class="copy-button" @click="copyAddressToClipboard">Ï£ºÏÜåÎ≥µÏÇ¨</button>
        </p>
        <div id="map" style="width:500px;height:400px;"></div>
      </div>
    </div>

    <!-- Î¶¨Î∑∞ ÏÑπÏÖò -->
    <div class="review-container">
      <h3>Î¶¨Î∑∞</h3>

      <!-- Î¶¨Î∑∞Í∞Ä ÏóÜÏùÑ Îïå -->
      <div v-if="hotelReviews && hotelReviews.length === 0">
        <p>Ïù¥ Ìò∏ÌÖîÏóê ÎåÄÌïú Î¶¨Î∑∞Í∞Ä ÏóÜÏäµÎãàÎã§.</p>
      </div>

      <!-- Î¶¨Î∑∞Í∞Ä ÏûàÏùÑ Îïå -->
      <div v-else>
        <transition-group name="fade" tag="div" class="review-list">
          <div
            v-for="(review, index) in visibleReviews"
            :key="index"
            class="review-grid"
          >
            <!-- Î¶¨Î∑∞ ÏÉÅÎã® -->
            <div class="review-top">
              <div class="review-rating">
                <span
                  v-for="star in 5"
                  :key="star"
                  class="star"
                  :class="{ filled: star <= review.rating }"
                >
                  ‚≠ê
                </span>
              </div>
              <div class="review-actions">
                <div class="review-date">
                  {{ reviewFormatDate(review.updateDate || review.writeDate) }}
                </div>
                <button
                  v-if="review.userId === loggedInUserId"
                  class="edit-button"
                  @click="openEditModal(review)"
                >
                  ÏàòÏ†ïÌïòÍ∏∞
                </button>
                <button
                  v-else
                  class="report-button"
                  @click="reportReviews(review.reviewId, review.userId)"
                >
                  Ïã†Í≥†ÌïòÍ∏∞
                </button>
              </div>
            </div>

            <!-- ÏûëÏÑ±Ïûê Î∞è Í∞ùÏã§ Ï†ïÎ≥¥ -->
            <div class="reviewer">{{ review.userName }}</div>

            <!-- Î¶¨Î∑∞ ÎÇ¥Ïö© -->
            <div class="review-content">{{ review.content }}</div>

            <!-- Ïù¥ÎØ∏ÏßÄ Í∞§Îü¨Î¶¨ -->
            <div
              class="review-images"
              v-if="review.imageUrl && review.imageUrl.length > 0"
            >
              <img
                v-for="(image, imgIndex) in review.imageUrl"
                :src="image"
                :key="imgIndex"
                class="review-image"
                @click="openLightbox(image)"
              />
            </div>
          </div>
        </transition-group>

        <!-- Îçî Î≥¥Í∏∞ Î≤ÑÌäº -->
        <div
          v-if="visibleReviewCount < hotelReviews.length"
          class="load-more-container"
        >
          <button @click="expandReviews" class="load-more-btn">
            ‚ûï Îçî Î≥¥Í∏∞
          </button>
        </div>
      </div>
    </div>

    <div class="room-list">
      <h3>Í∞ùÏã§ Ï†ïÎ≥¥</h3>
      <div v-if="hotel.rooms && hotel.rooms.length > 0">
        <div v-for="room in hotel.rooms" :key="room.roomId" class="room-card">
          <div class="room-image-container">
            <template v-if="room.roomImageUrl">
              <img :src="room.roomImageUrl" class="room-img" alt="Room Image" />
            </template>
            <template v-else>
              <div class="no-room-image">
                <p class="no-room-image-text">ÏóÖÏ≤¥Ï∏°ÏóêÏÑú Ï†úÍ≥µÎêú Ïù¥ÎØ∏ÏßÄÍ∞Ä ÏóÜÏäµÎãàÎã§</p>
              </div>
            </template>
          </div>

          <div class="room-info">
            <h4 class="room-name">{{ room.roomType }}</h4>
            <div class="avg-person">
              <img class="person-icon" src="https://yaimg.yanolja.com/stay/static/images/v3/icon_my.png" />
              <span class="avg-person-text">Í∏∞Ï§ÄÏù∏Ïõê {{ room.roomOccupancy }}Ïù∏</span>
            </div>
            <div class="reservation-info">
              <h5 class="reservation-text">ÏàôÎ∞ï</h5>
              <div class="check-info">
                Ï≤¥ÌÅ¨Ïù∏ <span v-html="formattedCheckIn"></span> ~ Ï≤¥ÌÅ¨ÏïÑÏõÉ <span v-html="formattedCheckOut"></span>
              </div>
              <h2 class="price">{{ room.roomPrice ? `${room.roomPrice.toLocaleString()}Ïõê` : "Í∞ÄÍ≤© Ï†ïÎ≥¥ ÏóÜÏùå" }}</h2>
              <div class="reservation-bottom">
                <div class="room-count">ÎÇ®ÏùÄ Í∞ùÏã§ {{ room.roomCount }}Í∞ú</div>
                  <button @click="move(room)" class="reservation_btn">ÏòàÏïΩ Î∞è ÏÉÅÏÑ∏Î≥¥Í∏∞</button>

              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-else>
        <div class="room-card">
          <div class="no-room-info-container">
            <p class="no-room-info-text">ÏóÖÏ≤¥Ï∏°ÏóêÏÑú Ï†úÍ≥µÎêú Í∞ùÏã§ Ï†ïÎ≥¥Í∞Ä ÏóÜÏäµÎãàÎã§.</p>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div v-else>
    <p>Ìò∏ÌÖî Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Ï§ëÏûÖÎãàÎã§...</p>
  </div>
</template>

<script>
/* global kakao */
import axios from "axios";
import { getReviewsByHotel, reportReview } from "@/api/api";
import { useAuthStore } from "@/store/register_login";

export default {
  name: "HotelDetails",
  data() {
    return {
      hotel: null,
      hotelReviews: [],
      visibleReviews: [],
      visibleReviewCount: 3,
      isFavorited: false,
      isLoggedIn: false,
    };
  },
  mounted() {
    this.fetchFavoriteStatus();
  },
  async created() {
    const authStore = useAuthStore();
    authStore.checkLoginStatus();
    this.isLoggedIn = authStore.LoggedIn;
    this.token = authStore.accessToken;

    await this.fetchHotelDetails();
    if (this.hotel && this.hotel.mapX && this.hotel.mapY) {
      this.loadKakaoMap();
    }
  },
  beforeUnmount() {
    // Kakao Map Ïä§ÌÅ¨Î¶ΩÌä∏Î•º Ï†úÍ±∞ÌïòÏó¨ Ï∂©Îèå Î∞©ÏßÄ
    const kakaoScript = document.querySelector("script[src*='//dapi.kakao.com/v2/maps/sdk.js']");
    if (kakaoScript) {
      kakaoScript.remove();
      delete window.kakao; // Ï†ÑÏó≠ kakao Í∞ùÏ≤¥ ÏÇ≠Ï†ú
    }
  },
  watch: {
    hotel(newHotel) {
      if (newHotel && newHotel.mapX && newHotel.mapY) {
        this.loadKakaoMap();
      }
    }
  },

  computed: {
  formattedCheckIn() {
    return this.hotel.checkIn ? this.hotel.checkIn.replace(/<br\s*\/?>/gi, '<br>') : "Ï†ïÎ≥¥ÏóÜÏùå";
  },
  formattedCheckOut() {
    return this.hotel.checkOut ? this.hotel.checkOut.replace(/<br\s*\/?>/gi, '<br>') : "Ï†ïÎ≥¥ÏóÜÏùå";
  }
},

  methods: {
    async fetchHotelDetails() {
      const hotelId = this.$route.params.id;
      try {
        const response = await axios.get(`/api/hotels/${hotelId}`);
        this.hotel = response.data; // HotelDetailDTO ÌòïÌÉúÎ°ú Îç∞Ïù¥ÌÑ∞ ÏàòÏã†
        console.log(this.hotel);

        await this.fetchHotelReviews(hotelId);
      } catch (error) {
        console.error("Ìò∏ÌÖî ÏÉÅÏÑ∏ Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò§Îäî Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
      }
    },
    move(room) {
      this.$router.push({
        params: { roomId: room.roomId },
        name: 'HotelRoom',
        state: {
          hotelName: this.hotel.name,
          roomName: room.roomType,
          roomPrice: room.roomPrice,
          checkIn: this.hotel.checkIn,
          checkOut: this.hotel.checkOut,
          roomId: room.roomId,
        },
      });
    },
    copyAddressToClipboard() {
      if (this.hotel && this.hotel.address) {
        navigator.clipboard.writeText(this.hotel.address)
          .then(() => {
            alert("Ï£ºÏÜåÍ∞Ä Î≥µÏÇ¨ÎêòÏóàÏäµÎãàÎã§.");
          })
          .catch(err => {
            console.error("Ï£ºÏÜå Î≥µÏÇ¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.", err);
          });
      }
    },
    loadKakaoMap() {
      const apiKey = process.env.VUE_APP_KAKAO_API_KEY;
      if (typeof kakao === "undefined") {
        const script = document.createElement("script");
        script.src = `//dapi.kakao.com/v2/maps/sdk.js?appkey=${apiKey}&autoload=false`;
        script.onload = this.initMap;  // Ïä§ÌÅ¨Î¶ΩÌä∏ Î°úÎìú ÌõÑ initMap Ìò∏Ï∂ú
        document.head.appendChild(script);
        console.log(process.env);
      } else {
        this.initMap(); // kakao Í∞ùÏ≤¥Í∞Ä Ïù¥ÎØ∏ ÏûàÏúºÎ©¥ Î∞îÎ°ú ÏßÄÎèÑ Ï¥àÍ∏∞Ìôî
      }
    },
    initMap() {
      kakao.maps.load(() => {
        const container = document.getElementById("map");
        const options = {
          center: new kakao.maps.LatLng(this.hotel.mapY || "Ï¢åÌëú‚ùå", this.hotel.mapX || "Ï¢åÌëú‚ùå"), // ÏßÄÎèÑÏùò Ï§ëÏã¨ Ï¢åÌëú
          level: 3, // ÏßÄÎèÑÏùò ÌôïÎåÄ Î†àÎ≤®
        };

        // Ïó¨Í∏∞ÏÑú mapÏùÑ ÏßÄÏó≠ Î≥ÄÏàòÎ°ú Ï†ïÏùò
        const map = new kakao.maps.Map(container, options); // ÏßÄÎèÑ ÏÉùÏÑ±

        // ÎßàÏª§Î•º ÏÉùÏÑ±ÌïòÍ≥† ÏßÄÎèÑÏóê ÌëúÏãú
        const markerPosition = new kakao.maps.LatLng(this.hotel.mapY || "Ï¢åÌëú‚ùå", this.hotel.mapX || "Ï¢åÌëú‚ùå");
        const marker = new kakao.maps.Marker({
          position: markerPosition,
        });
        marker.setMap(map);
      });
    },
    async fetchFavoriteStatus() {
      const token = this.token;
      const hotelId = this.$route.params.id;

      if (!this.isLoggedIn) {
        return;
      }

     try {
        const response = await axios.get(`/api/auth/favorites/status/${hotelId}`, {
          headers: {
            Authorization: `Bearer ${token}`
          }
        });
        this.isFavorited = response.data; // ÏÑúÎ≤ÑÏóêÏÑú Î∞õÏùÄ true/false Í∞íÏùÑ isFavoritedÏóê Î∞òÏòÅ
      } catch (error) {
        console.error("Ï∞ú ÏÉÅÌÉú Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®", error);
      }
    },
    async toggleFavorite() {
      const token = this.token;
       const hotelId = this.$route.params.id;
       if (!this.isLoggedIn) {
        alert("Î°úÍ∑∏Ïù∏ ÌõÑ Ï¶êÍ≤®Ï∞æÍ∏∞Î•º Ï∂îÍ∞ÄÌï† Ïàò ÏûàÏäµÎãàÎã§.");
        return;
      }
      const url = this.isFavorited
        ? `/api/auth/favorites/cancel/${hotelId}`
        : `/api/auth/favorites/${hotelId}`;

      try {
        await axios.post(url, {}, {
          headers: {
            Authorization: `Bearer ${token}`
          }
        });

        this.isFavorited = !this.isFavorited; // ÏÉÅÌÉú Î≥ÄÍ≤Ω ÌõÑ isFavorited Î∞òÏòÅ
      } catch (error) {
        console.error("Ï∞ú ÏÉÅÌÉú Î≥ÄÍ≤Ω Ïã§Ìå®", error);
      }
    },
    async fetchHotelReviews(hotelId) {
      try {
        const response = await getReviewsByHotel(hotelId); // API Ìò∏Ï∂ú
        this.hotelReviews = response.data; // Î¶¨Î∑∞ Îç∞Ïù¥ÌÑ∞Î•º Ï†ÄÏû•

        // Ï¥àÍ∏∞ visibleReviews ÏÑ§Ï†ï
        this.visibleReviews = this.hotelReviews.slice(
          0,
          this.visibleReviewCount
        );
      } catch (error) {
        console.error("Ìò∏ÌÖî Î¶¨Î∑∞ Ï°∞Ìöå Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
      }
    },
    reviewFormatDate(dateString) {
      const options = { year: "numeric", month: "2-digit", day: "2-digit" };
      return new Date(dateString).toLocaleDateString("ko-KR", options);
    },
    expandReviews() {
      const newCount = this.visibleReviewCount + 3;
      this.visibleReviewCount = newCount;
      this.visibleReviews = this.hotelReviews.slice(0, newCount);
    },
    async reportReviews(reviewId, reviewUser) {
      const authStore = useAuthStore();
      const token = sessionStorage.getItem("token");

      // Î°úÍ∑∏Ïù∏ ÌôïÏù∏
      if (!this.isLoggedIn) {
        alert("Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§");
        this.$router.push("/login");
        return;
      }

      // loggedInUserIdÏôÄ reviewId ÌôïÏù∏
      if (!reviewId) {
        alert("Ïã†Í≥† Ï†ïÎ≥¥Î•º ÌôïÏù∏Ìï† Ïàò ÏóÜÏäµÎãàÎã§.");
        console.error("Missing userId or reviewId");
        return;
      }

      console.log("Î¶¨Î∑∞ id : ", reviewId);
      console.log("Î¶¨Î∑∞ Ïú†Ï†Ä : ", reviewUser);

      // Î¶¨ÌÄòÏä§Ìä∏ Î∞îÎîî ÏÉùÏÑ±
      const reportData = {
        reporterId: authStore.userId, // ÌòÑÏû¨ Î°úÍ∑∏Ïù∏Îêú ÏÇ¨Ïö©Ïûê ID
        reportedId: reviewUser, // Ïã†Í≥† ÎåÄÏÉÅ ÏÇ¨Ïö©Ïûê ID
        reviewId: reviewId, // Ïã†Í≥† ÎåÄÏÉÅ Î¶¨Î∑∞ ID
      };

      try {
        // AxiosÎ•º ÏÇ¨Ïö©Ìïú Ïã†Í≥† ÏöîÏ≤≠
        const response = await reportReview(reportData, token);

        if (response.data) {
          alert("Ïã†Í≥†ÎêòÏóàÏäµÎãàÎã§.");
          console.log(response.data);

        }
      } catch (error) {
        alert("Ïã†Í≥† Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
        console.error("Axios request error:", error.response || error.message);
      }
    },
  },
};
</script>

